#bannerSwiper {
    width: 100%;
    height: 300px;
    border: 1px solid #ccc;
    position: relative;
    .swiper-pagination-bullet {
        background: #333 !important;
    }
}


.swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    background: #eee;
}
.dash {
    &-list {
    }
    &-item {
        width: 100%;
        padding: 1rem;
        .tlt {
            font-size: 1.2rem;
        }
        .list {
            display: flex;
            align-items: center;
            justify-content: flex-start;
        }
        .item {
            display: flex;
            width: 25%;
            justify-content: space-between;
            padding: 1rem;
            .img-wrap { width: 100%;
                img {width: 100%; max-width: 100%;}
            }
        }
    }
    &-header {display: flex; align-items: center; justify-content: space-between ; margin-bottom: 1rem;}
}
.more-btn {
    color: #2563EB !important;
}

@media screen and (max-width: 768px) {
    .dash {
        &-list {
        }
        &-item {
            width: 100%;
            padding: 1rem;
            .tlt {
                font-size: 1.2rem;
            }
            .list {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                flex-wrap: wrap;
            }
            .item {
                display: flex;
                width: 50%;
                justify-content: space-between;
                padding: 1rem;
                .img-wrap { width: 100%;
                    img {width: 100%; max-width: 100%;}
                }
            }
        }
        &-header {display: flex; align-items: center; justify-content: space-between ; margin-bottom: 1rem;}
    }

}
